	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Christmas Market Monday","date":"03 Dec 2006","slug":"/2006-12-03-christmas-market-monday/","tags":"Y2006"},"html":"<p>J decided that <a href=\"http://www.jbittner.com/germany/2006/12/4-december-2006-is-christmas-market.html\">tomorrow would be Christmas Market Monday</a>, when we all post pictures from our visits to our local Christmas Markets this past weekend. I usually don't go along with special blogging days, but since we were meeting Oma at the Christmas Market in Buchholz anyway, I brought my camera along. Just to be a non-conformist, I'm posting mine a day early. There's a <a href=\"http://flickr.com/photos/papascott/sets/72157594403825079/detail7\">set at flickr</a>, and a slideshow <a href=\"http://flickr.com/photos/papascott/sets/72157594403825079/show/\">here</a>. I tried to tell a story with the captions, so maybe the <a href=\"http://flickr.com/photos/papascott/sets/72157594403825079/detail/\">plain view</a> is better than the slideshow (where you have to click on each picture to see the caption).</p>\n<p><a href=\"http://flickr.com/photos/papascott/sets/72157594403825079/detail/\" title=\"Photo Sharing\"><img src=\"https://static.flickr.com/116/313145816_1459e156c7.jpg\" width=\"500\" height=\"375\" alt=\"IMG 1827\" /></a></p>\n<p>It would have been funnier to go to the <a href=\"http://www.hamburg.de/event.do?cid=6513353\">\"Santa Pauli\" Christmas Market</a> on Spielbudenplatz, which features booths from FC St. Pauli and a various sex shops, since we were in the area on Friday. Actually not, since it was so blatantly commercial and was most definately inappropriate for children. We didn't stop.</p>"},"next":{"fields":{"title":"No Accident","date":"02 Dec 2006","slug":"/2006-12-02-no-accident/"}},"previous":{"fields":{"title":"Do-It-Yourself Advent Calendar","date":"03 Dec 2006","slug":"/2006-12-03-do-it-yourself-advent-calendar/"}}},{"node":{"fields":{"title":"No Accident","date":"02 Dec 2006","slug":"/2006-12-02-no-accident/","tags":"Y2006"},"html":"<p>Headline from Spiegel Online:</p>\n<blockquote><p>\n  <a href=\"http://www.spiegel.de/panorama/0,1518,452163,00.html\">Accident at Reptile Show</a>: Crocodile Snaps, Circus Director Loses Thumb\n</p></blockquote>\n<p>Somehow that doesn't sound accidental.</p>"},"next":{"fields":{"title":"Jim Knopf and Christopher","date":"02 Dec 2006","slug":"/2006-12-02-jim-knopf-and-christopher/"}},"previous":{"fields":{"title":"Christmas Market Monday","date":"03 Dec 2006","slug":"/2006-12-03-christmas-market-monday/"}}},{"node":{"fields":{"title":"Jim Knopf and Christopher","date":"02 Dec 2006","slug":"/2006-12-02-jim-knopf-and-christopher/","tags":"Y2006"},"html":"<p><a href=\"http://www.flickr.com/photos/papascott/311920609/\" title=\"Photo Sharing\"><img src=\"https://static.flickr.com/105/311920609_d52e96b258_o.jpg\" width=\"500\" height=\"406\" alt=\"jimknopf\" /></a></p>\n<p>We went to see the musical version of <a href=\"http://tivoli.de/index.php?id=event843\">Jim Knopf und Lukas der Lokomotivfuhrer</a> at the Schmidt Theatre in Hamburg yesterday. As you can see, Christopher was very happy to have his picture (a scan of a polaroid) taken with the star. We can highly recommend it.</p>\n<p>As a bonus, here's the annual Christmas Cookie Cutter picture.</p>\n<p><a href=\"http://www.flickr.com/photos/papascott/311931666/\" title=\"Photo Sharing\"><img src=\"https://static.flickr.com/116/311931666_6999e98b23.jpg\" width=\"500\" height=\"375\" alt=\"IMG 1815\" /></a></p>"},"next":{"fields":{"title":"Major Medical Breakthrough","date":"01 Dec 2006","slug":"/2006-12-01-major-medical-breakthrough/"}},"previous":{"fields":{"title":"No Accident","date":"02 Dec 2006","slug":"/2006-12-02-no-accident/"}}}],"pathPrefix":"","first":false,"last":false,"index":174,"pageCount":1101}}

